import { Stack, Typography } from "@mui/material";
import { useState, useEffect } from "react";
import Card from '@mui/material/Card'
import CardContent from '@mui/material/CardContent';
import { NavDrawer } from ".";
import axios from "axios";

export default function Activity() {

    const [pastActivities, setActivities] = useState([]);

    useEffect(() => {
        getActivities();
    }, [])

    async function getActivities() {
        try {
            const response = await axios.get(global.route + `/api/activity/from`, { withCredentials: true });
            let act = [];
            for (let i = 0; i < response.data.activities.length; i++) {
                let acti = response.data.activities[i];
                let userName = await getCustomerName(acti.user_to);
                acti.userName = userName;
                act.push(acti);
            }
            setActivities(act);
            return response.data.activities;
        } catch (error) {
            console.error(error);
        }
    };

    async function getCustomerName(uid) {
        try {
            const response = await axios.get(global.route + `/api/users/${uid}`, { withCredentials: true });
            return response.data.name;
        } catch (error) {
            console.error(error);
        }
    };

    function getLocalTime(UTCtime) {
        var localDate = new Date(UTCtime);
        return localDate.toLocaleString("en-GB");
    }

    return (
        <div>
            <title>{document.title = "Past Activities"}</title>
            <NavDrawer />
            <Typography variant="h1">Activity Screen</Typography>
            {pastActivities.reverse().map((thing, idx) => (
                <Card key={idx} sx={{ mx: 5, my: 1 }} style={{ backgroundColor: "#F6F6F6" }}>
                    <CardContent>
                        <Stack direction="row" alignItems="center">
                            <Typography variant="h4">{thing.userName}</Typography>
                            <Typography sx={{ ml: 'auto' }} variant="h6">{getLocalTime(thing.createdAt).toString()}</Typography>
                        </Stack>
                        <Typography>Bought {thing.containers.length} containers</Typography>
                        {thing.containers.map((a, idy) => (
                            <Typography key={idy} sx={{ mx: 2 }}>{a.cid}</Typography>
                        ))}
                    </CardContent>
                </Card>
            ))}
        </div>
    )
}