import { Html5QrcodeScanner } from "html5-qrcode";
import { useState, useEffect } from "react";
import axios from "axios";
import Accordion from '@mui/material/Accordion';
import AccordionDetails from '@mui/material/AccordionDetails';
import AccordionSummary from '@mui/material/AccordionSummary';
import Typography from '@mui/material/Typography';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import Button from '@mui/material/Button';
import Stack from '@mui/material/Stack';
import Container from '@mui/material/Container';
import { NavDrawer } from ".";


import "./index.css";

export default function Scanner() {


  const [customers, setCustomers] = useState([]);
  const [expanded, setExpanded] = useState(false);
  const [selected, setSelected] = useState({ userid: "", containerids: "" });

  useEffect(() => {
    getStock();
    if (JSON.parse(localStorage.getItem("customers"))) {
      setCustomers(JSON.parse(localStorage.getItem("customers")));
    }
    const scanner = new Html5QrcodeScanner("reader", {
      qrbox: {
        width: 250,
        height: 250,
      },
      fps: 10,
    });

    scanner.render(success, error);

    function success(result) {
      let res = JSON.parse(result);
      scanner.pause();
      setTimeout(() => scanner.resume(), 200);
      if (res.hasOwnProperty("uid")) {
        userScanned(res.uid);
      } else if (res.hasOwnProperty("cid")) {
        containerScanned(res.cid);
      }
    }

    function error(error) {
      //console.warn(error);
    }
  }, []);

  useEffect(() => {
    localStorage.setItem("selected", JSON.stringify(selected))
  }, [selected])

  useEffect(() => {
    localStorage.setItem("customers", JSON.stringify(customers))
  }, [customers])

  //cids is container ids it's supposed to be an array
  //uid is the id of the user that the containers are going to be assigned to
  const updateContainer = async (uid, cids) => {
    try {
      const response = await axios.put(global.route + `/api/containers/info`, {
        containerIds: cids,
        userId: uid
      }, { withCredentials: true });
    } catch (error) {
      console.error(error);
    }
  };

  async function getCustomerName(uid) {
    try {
      const response = await axios.get(global.route + `/api/users/${uid}`, { withCredentials: true });
      return response.data.name;
    } catch (error) {
      console.error(error);
    }
  };

  const newActivity = async (uid, cids) => {
    try {
      const response = await axios.post(global.route + `/api/activity/`, {
        containerids: cids,
        user_to: uid
      }, { withCredentials: true });
    } catch (error) {
      console.error(error);
    }
  };

  //assign this to the button that submits when all containers for that customer is scanned
  function onFinishOrder(thing) {
    let uid = thing.userid;
    let cids = thing.containerids;
    if (cids.length !== 0) {
      updateContainer(uid, cids);
      newActivity(uid, cids);
      let cust = [];
      if (JSON.parse(localStorage.getItem("past activity"))) {
        cust = JSON.parse(localStorage.getItem("past activity"));
      }
      cust.push(thing);
      localStorage.setItem("past activity", JSON.stringify(cust));
    }
    let found = customers.findIndex((a) => (a.userid === uid));
    setCustomers(oldValues => {
      return oldValues.filter((_, i) => i !== found)
    })
    setExpanded(false);
  }

  const handleChange = (panel) => (event, isExpanded) => {
    event.preventDefault();
    setSelected(panel);
    setExpanded(isExpanded ? panel.userid : false);
  };

  async function userScanned(userid) {
    const cust = JSON.parse(localStorage.getItem("customers"));
    let custA = [];
    for (let i = 0; i < cust.length; i++) {
      custA.push(cust[i].userid);
    }
    if (userid && !custA.includes(userid)) {
      let userName = await getCustomerName(userid);
      setCustomers(prevCustomers => [...prevCustomers, { userid, containerids: [], userName }]);
    }
  }

  async function containerScanned(cid) {
    const cust = JSON.parse(localStorage.getItem("customers"));
    let containerA = [];
    for (let i = 0; i < cust.length; i++) {
      for (let j = 0; j < cust[i].containerids.length; j++) {
        containerA.push(cust[i].containerids[j]);
      }
    }
    const select = JSON.parse(localStorage.getItem("selected"));
    const stock = await getStock();
    if (!containerA.includes(cid) && stock.includes(cid)) {
      select.containerids.push(cid);
      let sUserid = select.userid;
      let found = cust.findIndex((a) => (a.userid === sUserid));
      setSelected(select);
      cust[found] = select;
      setCustomers(cust);
    }
  }

  function onClickDelete(containerid) {
    const cust = JSON.parse(localStorage.getItem("customers"));
    const select = JSON.parse(localStorage.getItem("selected"));
    let found = cust.findIndex((a) => (a.userid === select.userid));
    let oldContainers = select.containerids;
    let newlist = oldContainers.filter(oldContainers => oldContainers !== containerid);
    cust[found] = { userid: select.userid, containerids: newlist, userName: select.userName };
    setCustomers(cust);
    setSelected(cust[found]);
  }

  async function getStock() {
    let uid = JSON.parse(localStorage.getItem("User"))._id;
    try {
      const response = await axios.get(global.route + `/api/containers/${uid}`, { withCredentials: true });
      let cont = []
      for(let i =0; i < response.data.containers.length; i++){
        cont.push(response.data.containers[i]._id);
      }
      return cont;
    } catch (error) {
      console.error(error);
    }
  };

  function clearStorage() {
    localStorage.clear();
    setCustomers([]);
  }

  return (
    <div>
      <title>{document.title = "Scanner"}</title>
      <NavDrawer />
      <Stack display='flex' flex='1' sx={{ ml: 2, my: 1 }} direction="row" spacing={1}>
        <Stack display='flex' flex='1' sx={{ ml: 2, my: 1 }} direction="column" spacing={1} alignItems="center">
          <Typography variant="h3">Scan Customers and Containers</Typography>
          <Container id="reader" sx={{ width: "70vh" }} />
          <Button variant="outlined" onClick={() => { clearStorage(); window.location.reload() }}>Delete local</Button>
        </Stack>
        <Container sx={{ my: 5 }} maxWidth="md">
          <Typography variant="h3"> Current Customers</Typography>
          {customers.map((thing, idx) => (
            <Accordion key={idx} expanded={expanded === thing.userid} onChange={handleChange(thing)} style={{ backgroundColor: "#F6F6F6" }}>
              <AccordionSummary
                expandIcon={<ExpandMoreIcon />}
              >
                <Typography sx={{ width: '33%', flexShrink: 0 }}>
                  {thing.userName}
                </Typography>
              </AccordionSummary>
              <AccordionDetails>
                <Typography>
                  Current containers:
                </Typography>
                {thing.containerids.map((a, idy) => (
                  <Stack key={idy} sx={{ my: 1 }} direction="row" spacing={1} alignItems="center" >
                    <Typography> {a} </Typography>
                    <Button variant="outlined" onClick={() => { onClickDelete(a) }}>Delete</Button>
                  </Stack>
                ))}
                <Button variant="outlined" fullWidth onClick={() => { onFinishOrder(thing) }}>Finish order</Button>
              </AccordionDetails>
            </Accordion>
          ))}
        </Container>
      </Stack>
    </div >
  );
}
