import { Html5QrcodeScanner } from "html5-qrcode";
import { useState, useEffect } from "react";
import axios from "axios";
import Typography from '@mui/material/Typography';
import Stack from '@mui/material/Stack';
import Container from '@mui/material/Container';
import { NavDrawer } from ".";

import "./index.css";

export default function Stock() {

  const [stock, setStock] = useState([]);

  useEffect(() => {
    getStock();
    
    const scanner = new Html5QrcodeScanner("reader", {
      qrbox: {
        width: 250,
        height: 250,
      },
      fps: 5,
    });

    scanner.render(success, error);

    function success(result) {
      let res = JSON.parse(result);
      if (res.hasOwnProperty("bid")) {
        boxScanned(res.bid);
      }
    }

    function error(error) {
      //console.warn(error);
    }
  }, []);

  useEffect(() => {
    localStorage.setItem("stock", JSON.stringify(stock))
  }, [stock])

  async function getContainersFromBox(bid) {
    try {
      const response = await axios.get(global.route + `/api/storage/${bid}`, { withCredentials: true });
      return response.data.storage.container_ids;
    } catch (error) {
      console.error(error);
    }
  };

  async function getStock() {
    let uid = JSON.parse(localStorage.getItem("User"))._id;
    try {
      const response = await axios.get(global.route + `/api/containers/${uid}`, { withCredentials: true });
      let cont = []
      for(let i =0; i < response.data.containers.length; i++){
        cont.push(response.data.containers[i]._id);
      }
      setStock(cont);
      return cont;
    } catch (error) {
      console.error(error);
    }
  };

  const updateContainers = async (cids) => {
    try {
      const response = await axios.put(global.route + `/api/containers/info`, {
        containerIds: cids,
        userId: JSON.parse(localStorage.getItem("User"))._id
      }, { withCredentials: true });
    } catch (error) {
      console.error(error);
    }
  };

  async function boxScanned(bid) {
    const stock = JSON.parse(localStorage.getItem("stock"));
    let stockA = [];
    for (let i = 0; i < stock.length; i++) {
      stockA.push(stock[i]);
    }
    let containers = await getContainersFromBox(bid);
    updateContainers(containers);
    for (let i = 0; i < containers.length; i++) {
      if (!stockA.includes(containers[i])) {
        setStock(prevStock => [...prevStock, containers[i]]);
      }
    }
  }

  return (
    <div>
      <title>{document.title = "Scanner"}</title>
      <NavDrawer />
      <Stack display='flex' flex='1' sx={{ ml: 2, my: 1 }} direction="row" spacing={1}>
        <Stack display='flex' flex='1' sx={{ ml: 2, my: 1 }} direction="column" spacing={1} alignItems="center">
          <Typography variant="h3">Scan Box</Typography>
          <Container id="reader" sx={{ width: "70vh" }} />
        </Stack>
        <Container sx={{ my: 5 }} maxWidth="md">
          <Stack display='flex' flex='1' sx={{ ml: 2, my: 1 }} direction="column" spacing={1}>
            <Typography variant="h3"> Current Containers</Typography>
            <Typography variant="h5">{stock.length} containers in stock</Typography>
          </Stack>
          {stock.map((thing) => (
            <Typography> {thing} </Typography>
          ))}
        </Container>
      </Stack>
    </div >
  );
}
